nav>.conteiner{
    display:flex;
    justify-content: space-between;
    align-items: center;
}

nav{
    background:#12055c
}

.nav__logo{
    width:100px
}

.nav__link{
    margin:0 5%;
    text-decoration: none;
    transition:1s
}

.nav__link:hover{
    color:#fff;
}

.nav__links{
    display:flex;
    min-width:400px;
    flex-wrap:wrap;
}

@media (max-width:600px){
    .nav__links{
        display:flex;
        width:100%;
        min-width: 0px;
    }

    nav>.conteiner,.nav__links{
        flex-direction: column;
        align-items: center;
    }

    .nav__link{
        margin-top:5px;
    }
}