*{
  margin:0px;
  padding:0px;
}

.conteiner{
  max-width:1000px;
  margin:0 auto;
}

canvas{
  position:fixed;
  z-index: -1;
}


a,p,h1,img{
  opacity: 1;
}

p,ul{
  font-size:30px;
}

h1{
  font-size:80px;
}

body,a,button{
  color:rgb(255, 187, 0);
}


h2{
  font-size:50px;
  text-align:center;
}

@media (max-width:1000px){
  .conteiner{
    max-width: 600px;
  }
}

@media (max-width:728px) {
    h1{
      font-size:60px;
    }
}

@media (max-width:600px){
  .conteiner{
    max-width: 90%;
  }

  h1{
    font-size:40px
  }

  h2{
    font-size:30px;
  }
}

@media (max-width:352px){
  h1{
    font-size:30px;
  }
}